.Hiragana,
.Katakana {
  display: flex;
  flex-flow: row wrap;
}

.KanaMiniCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: 0 1%;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  flex: 0 0 16%;
}

.emptyCard {
  background: rgba(255, 255, 255, 0);
  border-radius: 20px;
  color: #000;
  margin: 0 1%;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  flex: 0 0 16%;
}

.KanaMiniCardKana,
.KanaMiniCardRomaji {
  font-size: 3.5vw;
  margin: 0;
  padding: 0;
}
